<i18n>
{
  "en": {
    "RemoveBattery": "Remove Portable battery",
    "JustMoment": "Portable battery is popping up. Just a moment, please.",
    "unit": "S",
    "customerService": "If not, please wait 1-3 minutes. If not, please re-lease or contact customer service",
    "credit": "Sorry, your credit is running low",
    "rechargeTheBalance": "In order to normally use the filling charge, please recharge the balance first.",
    "RechargeBalance": "Recharge balance",
    "Reminder": "Reminder",
    "borrowedTheBattery": "Have borrowed the battery, please return it and try again",
    "OK": "OK",
    "isNotAvailable": "The equipment is not available for the time being, please change it."
  },
  "zh": {
    "RemoveBattery": "取充电宝",
    "JustMoment": "充电宝正在弹出，请稍等",
    "unit": "秒",
    "customerService": "如未吐出充电宝，请耐心等待1-3分钟，如仍未吐出，请重新租借或联系客服",
    "credit": "余额不足",
    "rechargeTheBalance": "为了正常使用座充，请先充值余额",
    "RechargeBalance": "充值余额",
    "Reminder": "提示",
    "borrowedTheBattery": "当前已经借了充电宝，请归还充电宝后，再重试",
    "OK": "好的",
    "isNotAvailable": "该设备暂无可借充电宝，请换台设备使用"
  }
}
</i18n>

<template>
  <div class="takingCharger" ref="takeChanger">
    <head-top :title='$t("RemoveBattery")' :custom-back="true" @goBackHander="goBackHander" proportion="15:70:15"></head-top>
    <div class="container">
      <img src="../../../assets/scanning_images/baby-icon.png" alt="">
      <span>{{ $t("JustMoment") }}</span>
      <div class="countdown">
        <b>
          <strong>{{time}}</strong>{{ $t("unit") }}
        </b>
        <div class="countdown-bg"></div>
      </div>
      <div class="describe" ref="sizehuawei">{{ $t("customerService") }}</div>
    </div>

    <message-box  :id="7"
                  icon="defic"
                  :title="$t('credit')"
                  :describe="$t('rechargeTheBalance')"
                  :alert="$t('RechargeBalance')"
                  @boxHandle="boxHandle"></message-box>

    <message-box  :id="8"
                  icon="baby"
                  :title="$t('Reminder')"
                  :describe="$t('borrowedTheBattery')"
                  :alert="$t('OK')"
                  @boxHandle="boxHandle"></message-box>

    <toast :toastTip.sync="toastTip"
           :describe="$t('isNotAvailable')"
           position="bottom"></toast>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import headTop from '../../../components/Header/index'
import messageBox from '../../../components/MessageBox/index'
import toast from '../../../components/Toast/index'
export default {
  name: 'takingCharger',
  data () {
    return {
      timer: null, // 定时器
      timer2: null, // 计时器
      time: 60, // 时间
      toastTip: false
      // clientHeight:''
    }
  },

  components: {
    headTop,
    messageBox,
    toast
  },

  computed: {
    ...mapGetters([ 'chargerId' ])
  },

  mounted () {
    this.$nextTick(() => {
      this.clientHeight = document.documentElement.clientHeight
      this.$refs.takeChanger.style.height = document.documentElement.clientHeight
    })
    // window.onresize = () => {
    //   // 匹配华为虚拟按键
    //   console.log('窗口大小发生改变')
    //   console.log(window.screen.height)
    //   console.log(document.documentElement.clientHeight)
    //   if (document.documentElement.clientHeight < this.clientHeight) {
    //     this.$refs.takeChanger.style.marginTop = -40 + 'px'
    //   } else {
    //     this.$refs.takeChanger.style.marginTop = 0 + 'px'
    //   }
    // }
    this.countdown()
    this.dataInteractionResponse(data => {
      // 借出充电宝返回
      if (data.msgType === '0x5206') {
        if (data.result) {
          this.$router.push('/takeSuccessfully')
        } else {
          switch (data.data.errorCode) {
            case '0x830104':
              this.setGlobalBox({bol: true, id: 7})
              break
            default:
              let timer = setTimeout(() => {
                this.$router.push('/takeFailure')
                clearTimeout(timer)
              }, 2000)
          }
        }
      } else if (data.msgType === '0x5214') {
        // 借出归还充电宝统一返回
        if (data.result) {
          // if (data.data.type === 'LENT') {
          //   this.$router.push('/takeSuccessfully')
          // }
          this.$store.state.user.scanFlag = false
          this.$router.push('/takeSuccessfully')
        } else {
          switch (data.data.errorCode) {
            case '0x830104':
              this.setGlobalBox({bol: true, id: 7})
              break
            default:
              let timer = setTimeout(() => {
                this.$router.push('/takeFailure')
                clearTimeout(timer)
              }, 2000)
          }
        }
      }
    })

    // 借出归还充电宝统一请求
    this.dataInteractionRequest({
      msgType: '0x5213',
      imei: this.chargerId.imei,
      type: 'LENT'
    })

    if (this.ISDEBUG) {
      // this.timer2 = setTimeout(() => {
      //   this.$http('/scanning/1').then(res => {
      //     if (res.data.result) {
      //       this.$router.push({path: '/takeSuccessfully'})
      //     } else {
      //       this.$router.push({path: '/takeFailure'})
      //     }
      //   })
      // }, 4000)
    }
  },

  methods: {
    ...mapActions([
      'setGlobalBox'
    ]),
    countdown () {
      this.timer = setInterval(() => {
        if (--this.time <= 0) {
          clearInterval(this.timer)
          this.$router.push({path: '/takeFailure'})
        }
      }, 1000)
    },

    // 弹框
    boxHandle (confirm, cancel, id) {
      switch (id) {
        case 7:
          if (confirm) {
            this.$router.push({path: '/recharge'})
          }
          this.setGlobalBox({bol: false, id: 0})
          break
        case 8:
          this.setGlobalBox({bol: false, id: 0})
          break
      }
    },

    // 返回按钮
    goBackHander () {
      this.$router.back()
    }
  },

  beforeDestroy () {
    clearInterval(this.timer)
    clearTimeout(this.timer2)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@keyframes rotate {
  0%{
    transform: rotate(0deg)
  }
  50% {
    transform: rotate(180deg)
  }
  100%{
    transform: rotate(360deg)
  }
}
.takingCharger{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    padding-top: 25px;
    img{
      height: 455px;
    }
    span{
      display: block;
      font-size: 32px;
      margin: 40px 0 50px 0;
    }
    .countdown{
      .wh(370px, 370px);
      position: relative;
      margin: 0 auto;
      b{
        .f-center;
        left: 55%;
        font-size: 30px;
        strong{
          font-size: 100px;
          margin-right: 30px;
        }
      }
      .countdown-bg{
        .fs;
        background: url('../../../assets/scanning_images/loading.png') 0 0 no-repeat;
        background-size: cover;
        animation: rotate 2s linear infinite;
      }
    }
    .describe{
      font-size: 26px;
      text-align: left;
      padding: 0 5%;
      color: #999999;
      margin-top: 40px;
    }
  }
}
</style>



